.main-nav-item{
    display: flex;
}
.NavManu{
    /* background: ; */
    display: flex;
    flex-direction: column;
    width: 200px; 
    
     
    
}
.logo{
    margin-bottom: 0;
    line-height: 2;
    color: tomato;
    font-weight: bold;
    padding-bottom: 100px;
    font-size: 40px;
}
.pd-bottom{
    height: 300px;
}
i{
    margin-right: 5px;
    margin-bottom:10px;
}
.user-img{
    width: 30px;
    margin-right: 5px;
    border-radius: 50%;
    
}
@media screen and (max-width:600px) {
    .NavManu{
        /* background: ; */
        display: block;
        flex-direction: row;
        width: 100%; 
        
    }
    .logo{
        display: block;
        justify-content: center;
        align-items: center;
        padding-bottom: 0px;
        border-bottom: 2px solid tomato;
    }
    .pd-bottom{
        height: 0px;
    }
    i{
        margin-right: 10px;
    }
     
}