.GridItems{
    display: flex;
   
}
.main-box{
    padding: 40px;
    width: 80%;
}
.main-box h2{
    margin-bottom: 30px;
}
.pro-img{
    width: 30px;
    height: 30px;
    border-radius: 50%;
}
.main-box form{
    width: 50%;
    border: 2px solid rgb(197, 197, 197) ;
    padding: 20px;
}
.main-box form button{
    margin-top: 15px;
}
@media screen and (max-width:600px) {
    .main-box form{
        width: 100%;
        border: 2px solid rgb(197, 197, 197) ;
        padding: 20px;
    }
    .GridItems{
        display: block;
       
    }
    .main-box{ 
        width: 100%;
        padding: 10px;
    }
}